
.nologin {
    width: 100%;
    height: 4rem;
    text-align: center;
    position: absolute;
    top: 2rem;
    i {
        font-size: 0.8rem;
        color: #008489;
        line-height: 1rem;
    }
    h2 {
        font-size: 0.42rem;
        line-height: 1rem;
    }
    button {
        font-size: 0.3rem;
        color: #fff;
        line-height: 1rem;
        padding: 0 0.3rem;
        border: none;
        border-radius: 5px;
        background: #008489;
    }
}

.mine {
    width: 92%;
    margin: 0 auto;
    .mine_hd {
        width: 95%;
        margin: 0 auto;
        height: 2rem;
        border-bottom: 1px solid #eee;
        display: flex;
        h2 {
            font-size: 0.54rem;
            color: #333;
            line-height: 2rem;
            flex: 1;
        }
        .mine_avatar {
            flex: 1;
            padding: 0.5rem 0;
            text-align: right;
            img {
                display: inline-block;
                width: 1rem;
                height: 1rem;
                border: 1px solid #eee;
                border-radius: 50%;
                background: #eee;
            }
            input {
                display: none;
            }
        }
    }
    .mine_order {
        padding: 0.5rem 0.2rem;
        h2 {
            font-size: 0.4rem;
            margin-bottom: 0.5rem;
        }
        ul {
            display: flex; 
            box-shadow: 1px 5px 10px #eee;
            padding: 0.5rem 0;
            border-radius: 5px;
            li {
                flex: 1;
                text-align: center;
                p {
                    line-height: 0.6rem;
                    i {
                        font-size: 0.4rem;
                    }
                }
            }
        }
    }
    .mine_menu {
        margin-bottom: 0.9rem;
        li {
            width: 100%;
            border-top: 1px solid #eee;
            line-height: 1.5rem;
            display: flex;
            padding: 0 0.2rem;
            box-sizing: border-box;
            p {
                flex: 1;
            }
            i {
                flex: 1;
                text-align: right;
            }
        }
    }
}